import './ComplexCard.scss'

function FormCard() {
  return (
    <form className='form'>
      <h1 className="form-title">  </h1>
      <div className="form__container">
        <input type="tel" className="form__container-input form__container-item" placeholder=' '/>
        <input type="name" className="form__container-input form__container-item" placeholder=" '"/>
        <button className="main-button form-button form__container-item"></button>
      </div>
    </form>
  )
}

export default FormCard